import React from 'react'
import styled from 'styled-components'

import info from '../images/info.jpg'

const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: flex-start;
	margin-top: 20px;
	width: 100%;
	height: 50%;
`
const InnerDiv = styled.div`
	display: flex;
	flex-direction: column;
	border: 1px solid rgba(0,0,0,.1);
	z-index: 2;
	background: #fff;
	width: 70%;
	padding: 10px;
	margin-bottom: 10px;
	p {
		font-size: 1.2em;
	}
	@media (min-width: 1000px) {
		flex-direction: row;
	}
`
const InfoPic = styled.div`
	img {
		width: 100%;
	}
`
const InfoText = styled.div`
	color: #42b4e4;
	p {
		line-height: 1;
	}
`

export default () => (
	<Container>
		<InnerDiv>
			<InfoPic>
				<img src={info} alt="information" />
			</InfoPic>
			<InfoText>
				<h2>Partner Fish Plant</h2>
				<p><strong>BEOTHIC FISH PROCESSORS LTD</strong></p>
				<p>VALLEYFIELD, NEW FOUNDLAND, CANADA</p>
				<p><strong>KA'LE BAY SEAFOOD LTD</strong></p>
				<p>GRACE BAY, NOVA SCOTIA, CANADA</p>
				<p>MULTI SPECIES PLANT</p>
				<p>AND PRIMARY PROCESSING LICENSES</p>
				<p>QUALITY AND INSPECTION:</p>
				<p>HACCCP AND CFIA</p>
				<p>(CANADIAN FOOD INSPECTION AGENCY) APPROVED.</p>
			</InfoText>
		</InnerDiv>
	</Container>
)


